import React, { useRef } from 'react'
import Slider from 'react-slick'

export const PersonalSlider = () => {
    const refSlider = useRef(null)

    const nextSlide = () => {
        document.getElementById('nextBtnSlider').classList.add('activeBtn')
        refSlider.current.slickNext()
        setTimeout(() => {
            document
                .getElementById('nextBtnSlider')
                .classList.remove('activeBtn')
        }, 500)
    }
    const prevSlide = () => {
        document.getElementById('prevBtnSlider').classList.add('activeBtn')
        refSlider.current.slickPrev()
        setTimeout(() => {
            document
                .getElementById('prevBtnSlider')
                .classList.remove('activeBtn')
        }, 500)
    }
    const settings = {
        infinite: true,
        speed: 500,
        swipeToSlide: true,
        slidesToShow: 5,
        slidesToScroll: 1,
    }
    const slidesArray = [
        {
            id: 1,
            className: 'slide1',
            isBig: ' ',
        },
        {
            id: 2,
            className: 'slide2',
            isBig: ' ',
        },
        {
            id: 3,
            className: 'slide3',
            isBig: ' ',
        },
        {
            id: 4,
            className: 'slide4',
            isBig: ' ',
        },
        {
            id: 5,
            className: 'slide5',
            isBig: ' ',
        },
        {
            id: 1,
            className: 'slide1',
            isBig: ' ',
        },
        {
            id: 2,
            className: 'slide2',
            isBig: ' ',
        },
        {
            id: 3,
            className: 'slide3',
            isBig: ' ',
        },
        {
            id: 4,
            className: 'slide4',
            isBig: ' ',
        },
        {
            id: 5,
            className: 'slide5',
            isBig: ' ',
        },
    ]

    const onSlideEnter = (item) => {
        slidesArray.forEach((element) => {
            if(element.id === item.id) element.className += 'bigSlide'
            else element.className += ' smallSlide'
        })
        // item.isBig = ' bigSlide'
    }

    const onSlideLeave = () => {
        slidesArray.forEach((element) => {
            element.isBig = 'smallSlide'
        })
    }

    return (
        <div className="personalSlider">
            <Slider ref={refSlider} className="personal-slider" {...settings}>
                {slidesArray.map((item) => (
                    <div
                        style={{ width: 100 }}
                        onMouseEnter={() => onSlideEnter(item)}
                        onMouseLeave={() => onSlideLeave()}
                        key={item.id}
                        className={`${item.className} ${item.isBig}`}
                    />
                ))}
            </Slider>
            <button
                id="prevBtnSlider"
                className="prev-slider"
                onClick={() => prevSlide()}
            />
            <button
                id="nextBtnSlider"
                className="next-slider"
                onClick={() => nextSlide()}
            />
        </div>
    )
}
